
#about {
    padding: 60px 20px;
    text-align: center;
    font-size: x-large;
}

.about-title {
    margin: 40px 0;
    font-size: 2em;
    text-align: center;
    padding: 100px;
}

.expertise-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr)); 
    gap: 60px;
    font-size: larger; 
}

.expertise-item {
    background-color: #fff; 
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    border-radius: 10px; 
    padding: 40px; 
    text-align: center;
    font-family: 'Times New Roman', Times, serif;
    font-size: larger;
}
.expertise-heading {
    margin: 10px 0;
}

#expertise {
    padding-top: 60px; 
    margin-top: -60px; 
}

html {
    scroll-behavior: smooth;
}