/* Global Styles */
body {
    margin: 0;
    padding: 0;
    background-image: url("sea.jpg");
    background-repeat: no-repeat;
    background-size: cover; 
    min-height: 150vh;
    background-position: center; 
    font-family: 'times new roman', sans-serif;
}

/* Header Styles */
header {
    background-color: rgb(179, 167, 167)  ;
    color: #333;
    padding: 4px; 
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

header nav {
    font-size: larger;
    display: flex;
    justify-content: flex-end;
}

header nav ul {
    list-style-type: none; 
    margin: 0; 
    padding: 0; 
    display: flex; 
}

header nav li {
    color: red;
    margin-left: 30px;
}

header nav a {
    color: black; 
    text-decoration: none;
    font-size: 1.5em; 
}

header nav a:hover {
    color: red; 
}

/* Introduction Section Styles */
.about-content {
    display: flex;
    font-size: xx-large;
    align-items: center;
    justify-content: space-between;
    padding: 10px; 
}

.about-text {
    flex: 1;
    margin-right: 70px;
}

.about-image {
    flex: 1;
}

.oval-image {
    width: 350px;
}


form {
    display: flex;
    flex-direction: column;
    max-width: 400px;
    margin: auto;
}

label {
    margin-top: 10px;
}

input,
textarea {
    padding: 10px;
    margin-top: 5px;
    border-radius: 5px;
    border: 1px solid #ccc;
}

button {
    margin-top: 15px;
    padding: 10px;
    background-color: #28a745;
    color: white;
    border: none;
    border-radius: 5px;
}
/* Footer Styles */
footer {
   background-color:#333; 
   color:white; 
   padding :20px ;
   text-align:center ;
}

/* Error and Success Messages Styles */
.error {
   color:red ; 
   margin-top :10px ; 
}

.success {
   color :green ; 
   margin-top :10px ; 
}

/* Media Queries for Responsiveness */
@media (max-width:768px) {
   .about-content{
      flex-direction :column ;
      text-align :center ;
   }

   .oval-image{
      width :80% ; 
      height:auto ; 
   }
}
/* In index.css */
html {
    scroll-behavior: smooth; 
}